* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Nanum Gothic', sans-serif;
}
li {
  list-style: none;
}

.header {
  width: 100%;
  height: 60px;
  background-color: #fff;
}
.header .inner {
  width: 1440px;
  height: 60px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  position: relative;
}
.header .inner .logo {
  margin-right: 50px;
  padding-left: 10px;
  padding-top: 5px;
}
.header .inner .gnb {
  display: flex;
}
.header .inner .gnb li {
  font-size: 18px;
  margin-right: 25px;
  color: #353015;
}
.header .inner .gnb li:last-child {
  margin-right: 0;
}
.header .inner .menu {
  display: flex;
  align-items: center;
  position: absolute;
  right: 10px;
}
.header .inner .menu img{
  width: 35px;
  margin-right: 10px;
}
.menu .cart {
  position: relative;
  cursor: pointer;
}
.menu .cart .shopping_cart{
  position: absolute;
  width: 20px;
  height: 20px;
  top: 0;
  right: 5px;
  background-color: tomato;
  border-radius: 50%;
}
.menu .cart .shopping_cart p{
  position: relative;
  top: 2px;
  
  background: transparent;
  font-size: 13px;
  font-weight: bold;
  color: #fff;
}
.menu .user {
  cursor: pointer;
}