*{
  font-family: 'Nanum Gothic', sans-serif;
}

.detail {
  max-width: 1440px;
  margin: 0 auto;
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 80px 0;
}
.product_img{
  width: 500px;
  display: flex;
  flex-direction: column;
  margin-right: 50px;
}
.product_img img{
  border: 1px solid #c4c4c4;
}
.product_info {
  width: 80%;
  display: flex;
  flex-direction: column;
  text-align: left;

}
.product_info .explain{
  font-size: 18px;
  line-height: 23px;
  color: #767676;
  margin-bottom: 16px;
}
.product_info .name{
  font-size: 36px;
  line-height: 45px;
  color: #000;
  font-weight: bold;
  margin-bottom: 20px;
}
.product_info .price{
  font-size: 36px;
  line-height: 45px;
  color: #000000;
  margin-bottom: 138px;
}
.delivery {
  width: 150px;
  height: 20px;
  margin-bottom: 20px;
}
.delivery p {
  font-size: 16px;
  line-height: 20px;
  color: #767676;
}
.line {
  width: 600px;
  height: 2px;
  background: #c4c4c4;
  margin-bottom: 30px;
}
.amount {
  position: relative;
  width: 150px;
  height: 50px;
  border: 1px solid #c4c4c4;
  border-radius: 5px;
  margin-bottom: 30px;
}
.amount .minus {
  position: absolute;
  width: 20px;
  height: 20px;
  top: 50%;
  left: 14px;
  transform: translateY(-50%);
  cursor: pointer;
}
.amount .count {
  position: absolute;
  width: 56px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #c4c4c4;
  border-top:none;
  border-bottom:none;
}
.amount .plus {
  position: absolute;
  width: 20px;
  height: 40px;
  top: 50%;
  right: 14px;
  transform: translateY(-50%);
  cursor: pointer;
}
.total {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 30px;
}
.total .sum_price {
  font-size: 18px;
  font-weight: 500;
  line-height: 23px;
  color: #000;
}
.total .total_info {
  font-size: 18px;
  line-height: 23px;
  color: #767676;
}
.total .total_info .total_count {
  font-size: 18px;
  line-height: 23px;
  color: #5F5A42;
}
.total .total_info .total_count::after {
  content: "|";
  width: 5px;
  height: 23px;
  color: #c4c4c4;
  padding: 0px 11px;
}
.total .total_info .total_price {
  font-size: 36px;
  line-height: 45px;
  color: #5F5A42;
}
.btn {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.btn .btn_buy {
  width: 416px;
  height: 60px;
  border-radius: 5px;
  background: #5F5A42;
  border: none;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  line-height: 22px;
  color: #ffffff;
  margin-right: 44px;
}
.btn .btn_buy:hover {
  background-color: #353015;
  transition: .3s;
}
.btn .btn_cart:hover {
  background-color: #353015;
  transition: .3s;
}
.btn .btn_cart {
  width: 200px;
  height: 60px;
  border-radius: 5px;
  background: #968F6B;
  border: none;
  cursor: pointer;
  font-size: 18px;
  font-weight: bold;
  line-height: 22px;
  color: #ffffff;
}

@media (max-width: 1440px) {
  .detail {
    flex-direction: column;
  }
  .product {
    width: 100%;
    margin: 0;
    padding: 20px;
    padding-bottom: 0;
  }
  .total .total_price {
    font-size: 24px;
  }
  .total .sum_price {
    margin-right: 24px;
  }
  .btn {
    justify-content: center;
  }
  .btn .btn_buy {
    width: 70%;
  }
  .btn .btn_cart {
    width: 30%;
  }
  .line {
    width: 100%;
  }
  .product_info .price {
    margin-bottom: 50px;
  }
}

@media (max-width: 380px) {
  .product_info .explain,
  .btn .btn_cart,
  .btn .btn_buy {
    font-size: 14px;
  }

  .product_info .name,
  .product_info .price,
  .total .total_price {
    font-size: 20px;
  }

  .total {
    display: block;
  }
