
.cart_title {
  display: flex;
  justify-content: center;
  max-width: 1440px;
  padding-top: 60px;
  margin: 0 auto;
}
.list_head {
  max-width: 1440px;
  height: 60px;
  margin: 0 auto;
  margin-top: 52px;
  background: #f2f2f2;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  place-items: center;
}
.list_head span {
  font-weight: bold;
}


/* cart_list */
.cart_list {
  position: relative;
  max-width: 1440px;
  height: 200px;
  border: 1px solid #c4c4c4;
  border-radius: 10px;
  margin: 0 auto;
  margin-top: 35px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  place-items: center;
}
.cart_list_box {
  display: flex;
  align-items: center;
}
.cart_list_img img{
  width: 120px;
  border-radius: 5px;
  border: 1px solid #c4c4c4;
}
.cart_list_info {
  width: 150px;
  margin-left: 30px;
}
.cart_list_info .product_name {
  font-size: 16px;
  line-height: 22px;
  font-weight: bold;
  margin-bottom: 10px;
}
.cart_list_info .product_price {
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 25px;
}
.cart_list_info .delivery {
  font-size: 14px;
  line-height: 18px;
  color: #767676;
}
.cart_list_count {
  position: relative;
  width: 110px;
  height: 45px;
  border: 1px solid #c4c4c4;
  border-radius: 5px;
}
.cart_list_count .minus{
  position: absolute;
  width: 10px;
  top: 50%;
  left: 14px;
  transform: translateY(-50%);
  cursor: pointer;
}
.cart_list_count .count{
  position: absolute;
  width: 38px;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border: 1px solid #c4c4c4;
  border-top:none;
  border-bottom:none;
}
.cart_list_count .plus{
  position: absolute;
  width: 10px;
  height: 18px;
  top: 50%;
  right: 14px;
  transform: translateY(-50%);
  cursor: pointer;
}
.cart_list_price .total_price {
  font-size: 18px;
  line-height: 23px;
  font-weight: bold;
}
.cart_list_remove {
  position: absolute;
  top: 15px;
  right: 20px;
  width: 20px;
  height: 15px;
  cursor: pointer;
}

/* total */
.total {
  max-width: 1440px;
  height: 150px;
  background: #f2f2f2;
  border-radius: 10px;
  margin: 0 auto;
  margin-top: 35px;
  margin-bottom: 35px;
  display: flex;
  justify-content: space-around;
  place-items: center;
}
.total .cart_total,
.sale .total_sale,
.delivery .total_delivery{
  font-size: 16px;
  line-height: 20px;
  margin-bottom: 12px;
}
.total .cart_total_price,
.sale .total_sale_price,
.delivery .total_delivery_price {
  font-size: 24px;
  line-height: 30px;
  color: #000000;
}
.payment .total_payment {
  font-size: 16px;
  font-weight: bold;
  line-height: 20px;
  color: #000000;
  margin-bottom: 12px;
}
.payment .total_payment_price {
  font-size: 30px;
  font-weight: bold;
  line-height: 30px;
  color: #5F5A42;
}
.btn .btn_buy {
  margin-right: 20px;
  width: 200px;
  height: 68px;
  background: #968F6B;
  border-radius: 5px;
  border: none;
  font-weight: bold;
  font-size: 24px;
  line-height: 30px;
  color: #fff;
  cursor: pointer;
}
.btn .btn_buy:hover {
  background: #5F5A42;
  transition: .3s;
}


.cart_empty {
  width: 100%;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.cart_empty h2{
  font-size: 24px;
  margin-bottom: 15px;
}
.cart_empty p{
  font-size: 20px;
  line-height: 18px;
  color: #767676;
}