.product {
  /* width: 1440px;
  margin: 0 auto;
  background-color: antiquewhite;
  padding: 80px 0;
  display: flex; */
  display: flex;
  flex-direction: column;
}
.product .product-img img{
  width: 304px;
  height: 449px;
  object-fit: cover;
  border: 1px solid #c4c4c4;
  margin-bottom: 20px;
}
.product .product-name{
  margin-bottom: 10px;
  font-size: 24px;
  font-weight: bold;
}
.product .product-explain{
  margin-bottom: 15px;
  font-size: 20px;
  color: #979797;
}
.product .product-price {
  font-size: 22px;
  font-weight: bold;
  margin-bottom: 80px;
}
a {
  text-decoration: none;
  color: #000;
}